<template>
  <div>
    <div class="nav-bar">
      <div class="nav-bar-left" @click="goBack()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-title">发起竞价</div>
    </div>
    <div class="container">
      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>基本信息</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <van-form>
            <van-field v-model="bidform.xmmc" :error-message="message1" required label="项目名称" label-width="100" placeholder="请输入项目名称" @blur="check(0)" @input="check(0)" />
            <van-field v-model="bidform.qpj" :error-message="message2" type="number" required label="起拍价" oninput="this.value = this.value.match(/^[1-9]\d*$/)?this.value:''" label-width="100" placeholder="请输入起拍价格" @blur="check(1)" @input="check(1)" />
            <van-field label="供应商征集方式" required label-width="100" :error-message="message3">
              <template #input>
                <van-radio-group v-model="bidform.gysxzfs" direction="horizontal" @change="check(2)">
                  <van-radio name="1">公开</van-radio>
                  <van-radio name="2">邀请</van-radio>
                </van-radio-group>
              </template>
            </van-field>
            <van-field v-model="bidform.lxr" type="text" label="联系人" label-width="100" placeholder="请输入联系人名称" />
            <van-field v-model="bidform.lxdh" type="text" label="联系方式" label-width="100" placeholder="请输入联系方式" />
            <van-field v-model="bidform.email" type="text" :error-message="message4" required label="邮箱" label-width="100" placeholder="请输入邮箱" @blur="check(3)" @input="check(3)" />
            <van-field v-model="bidform.bz" rows="1" autosize type="textarea" label="备注" label-width="100" placeholder="请输入备注信息" />
            <div class="page-list">
              <div class="pagelist-left">附件</div>
              <div class="pagelist-right left" style="margin-left: 0">
                <XyUploadPlus
                  ref="uploadEl"
                  style="width:100%;"
                  filed="test1"
                  :filegroup="filegroup"
                  @getFileChangeInfo="getFileChangeInfo"
                />
              </div>
            </div>
          </van-form>
        </div>
      </div>

      <div v-if="bidform.gysxzfs==='2'" class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>选择供应商</h2>
            </div>
            <div class="title-right" @click="showGysPopup()">新增供应商 <i class="iconfont iconarrowright" /></div>
          </div>
          <!-- 通用标题 end -->

          <div class="common-list">
            <van-swipe-cell v-for="(item,index) of xzGysList" :key="index">
              <div class="list">
                <h3><div class="tit">{{ item.gsmc }}</div></h3>
                <div class="info">
                  <span><i class="iconfont iconlianxiren" />{{ item.lxrmc }}</span>
                  <span><i class="iconfont iconshouji" />{{ item.lxrdh }}</span>
                  <span><i class="iconfont iconyoujian" />{{ item.email }}</span>
                  <span><i class="iconfont iconzhengshubg" />{{ item.shtyxydm }}</span>
                </div>
              </div>
              <template #right>
                <van-button square text="删除" type="danger" class="delete-button" @click="deleteGys(item.jgbh)" />
              </template>
            </van-swipe-cell>
          </div>

        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>竞价时间设置</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <van-form>
            <van-field
              v-model="bidform.jjsj"
              label="竞价时间"
              required
              readonly
              label-width="100"
              :error-message="message5"
              placeholder="请选择竞价时间"
              @click="show = true"
            />

            <div class="spe-list">
              <div class="spe-left"><span class="tit">竞价持续时间</span><span style="margin-left:0.22rem">{{ getTime() }}</span></div>
            </div>
            <van-field label="是否限制报价次数" required label-width="100" :error-message="message6">
              <template #input>
                <van-radio-group v-model="bidform.sfxzbjcs" direction="horizontal" @change="check(5)">
                  <van-radio name="1">是</van-radio>
                  <van-radio name="0">否</van-radio>
                </van-radio-group>
              </template>
            </van-field>
            <van-field v-if="bidform.sfxzbjcs!=='0'" v-model="bidform.bjcs" :disabled="bidform.sfxzbjcs==='0'" label="报价次数" oninput="this.value = this.value.match(/^[1-9]\d*$/)?this.value:''" label-width="100" placeholder="请输入报价次数" />
            <van-field label="是否允许延时竞价" required label-width="100" :error-message="message8">
              <template #input>
                <van-radio-group v-model="bidform.sfrxysjj" direction="horizontal" @change="check(7)">
                  <van-radio name="1">是</van-radio>
                  <van-radio name="0">否</van-radio>
                </van-radio-group>
              </template>
            </van-field>
            <div v-if="bidform.sfrxysjj!=='0'" class="page-list block">
              <div class="pagelist-left">延时竞价规则</div>
              <div class="pagelist-right">
                <div style="display: inline-block;line-height: 25px;">
                  离正式竞价结束时间前
                  <input v-model="bidform.jjgz1" :disabled="bidform.sfrxysjj==='0'" class="field-input" placeholder="请输入" oninput="this.value = this.value.match(/^[1-9]\d*$/)?this.value:''" style="width: 80px;margin: 0 5px;">
                  分钟之内产生新的报价后,反拍结束时间自动延长
                  <input v-model="bidform.jjgz2" :disabled="bidform.sfrxysjj==='0'" class="field-input" placeholder="范围1~60" oninput="this.value = this.value.match(/^([1-9]|[1-5][0-9])$/)?this.value:''" style="width: 80px;margin: 0 5px;">
                  分钟
                </div>
              </div>
            </div>
            <van-field v-if="bidform.sfrxysjj==='1'" label="是否限制延时次数" required label-width="100" :error-message="message7">
              <template #input>
                <van-radio-group v-model="bidform.sfrxyscs" direction="horizontal" @change="check(6)">
                  <van-radio name="1">是</van-radio>
                  <van-radio name="0">否</van-radio>
                </van-radio-group>
              </template>
            </van-field>
            <van-field v-if="bidform.sfrxyscs!=='0' && bidform.sfrxysjj==='1'" v-model="bidform.yscs" :disabled="bidform.sfrxyscs==='0'" oninput="this.value = this.value.match(/^[1-9]\d*$/)?this.value:''" label="延时次数" label-width="100" placeholder="请输入延时次数" />
          </van-form>
        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>出价步长设置</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <van-form>
            <van-field label="出价步长设置" required label-width="100" :error-message="message9">
              <template #input>
                <van-radio-group v-model="bidform.cjbcsz" direction="horizontal" @change="cjfsChange()">
                  <van-radio name="1">按绝对数量</van-radio>
                  <van-radio name="2">按百分比</van-radio>
                </van-radio-group>
              </template>
            </van-field>
            <van-field
              v-if="bidform.cjbcsz==='2'"
              v-model="bidform.jdsl"
              placeholder="范围：1~100"
              size="small"
              style="width:75%"
              oninput="this.value = this.value.match(/^100$|^(\d|[1-9]\d)(\.\d+)*$/)?this.value:''"
              required
              label="按百分比"
              :error-message="message10"
              label-width="100"
              @input="check(9)"
              @blur="check(9)"
            />
            <van-field
              v-else
              v-model="bidform.jdsl"
              label-width="100"
              required
              size="small"
              placeholder="请输入数量"
              style="width:75%"
              label="按绝对数量"
              :error-message="message10"
              oninput="this.value= this.value.match(/\d+(\.\d{0,1000})?/) ? this.value.match(/\d+(\.\d{0,1000})?/)[0] : ''"
              @input="check(9)"
              @blur="check(9)"
            />

          </van-form>
        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>商品信息</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <div class="goodscart-item">

            <div v-for="(spxx,indexs) of spxxlist" :key="indexs">
              <div v-for="(item,index) of spxx.productList" :key="index" class="goodsshow-loop">
                <div class="goodsshow-list">
                  <div class="goodsshow-c">
                    <div class="goodsshow-img" @click="imgPreview(item.imgSrc?item.imgSrc:defaultImg)">
                      <img :src="item.imgSrc" :onerror="defaultImg">
                    </div>
                    <div class="goodsshow-c-info">
                      <div class="goodsshow-tit" @click="toProductDetail(item.id,item.name)">{{ item.name }}</div>
                      <div class="goodsshow-tag">
                        <van-tag plain type="primary">{{ item.sourceName }}</van-tag>
                      </div>
                      <div class="goodsshow-info">
                        <div class="price">{{ item.price_now }}元</div>
                        <div class="num"><van-stepper :key="key" v-model="item.pcount" max="99999" min="1" integer @change="calPrice(item.pcount,item.cart_id,indexs,index)" /></div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <!-- <div class="goodsshow-loop">
              <van-swipe-cell>
                <div class="goodsshow-list">
                  <div class="goodsshow-c">
                    <div class="goodsshow-img">
                      <img src="@/assets/mall-images/goods/goods2.png">
                    </div>
                    <div class="goodsshow-c-info">
                      <div class="goodsshow-tit">国标A+级洗净能力 智能空气洗 互联网洗烘一体机10kg</div>
                      <div class="goodsshow-tag">
                        <van-tag plain type="primary">京东商城</van-tag>
                      </div>
                      <div class="goodsshow-info">
                        <div class="price">2999元</div>
                        <div class="num"><van-stepper v-model="num" /></div>
                      </div>
                    </div>
                  </div>
                </div>
                <template slot="right">
                  <van-button square type="danger" text="删除" @click="DeleteSp()" />
                </template>
              </van-swipe-cell>
            </div> -->

          </div>
        </div>
      </div>

    </div>

    <!-- 竞价时间弹窗 begin -->
    <van-calendar v-model="show" type="range" color="#1890FF" value="" @confirm="onConfirm" />
    <!-- 竞价时间弹窗 end -->
    <div class="common-space" />
    <!-- 底部按钮 begin -->
    <div class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="gobidsuccess('1')">保存</van-button></li>
        <li><van-button type="info" round size="large" @click="gobidsuccess('2')">提交</van-button></li>
      </ul>
    </div>
    <!-- 底部按钮 end -->

    <!-- 选择人员弹出层 beign -->
    <van-popup v-model="showGys" closeable position="bottom" :style="{ height: '100%' }">
      <div class="common-popup">
        <div class="popup-title">选择供应商</div>
        <div class="content">

          <van-sticky class="van-sticky-approve">
            <div class="close-button">
              <i class="iconfont iconguanbi" @click="showGys = false" />
            </div>
            <van-search v-model="gysmc" placeholder="请输入供应商名称" show-action><div slot="action" @click="onSearch">搜索</div></van-search>
          </van-sticky>
          <common-list
            ref="ggcommonlist"
            class="common-list"
            :post-url="list.postUrl"
            :req-data="list.reqData"
            :data-name="list.dataName"
            @returnData="getPrizeLog($event)"
          >
            <div v-for="(item,index) of GysList" :key="index" :class="checkIds.indexOf(item.jgbh)!==-1?'select-renyuan active':'select-renyuan'" @click="checkGys(index)">
              <div class="renyuan-l">
                <i class="iconfont iconxuanzhong" />
              </div>
              <div class="renyuan-m">
                <div class="name">{{ item.gsmc }}</div>
                <div class="info"><span>联系人：{{ item.lxrmc }}</span><span>联系电话：{{ item.lxrdh }}</span></div>
              </div>
            </div>
          </common-list>
        </div>
      </div>
      <div class="common-space" />
      <div class="common-space" />
      <div class="common-space" />
      <div class="common-space" />
      <!-- 底部按钮 begin -->
      <div class="foot-btn fixed">
        <ul>
          <li><van-button type="info" round size="large" @click="close">确定</van-button></li>
        </ul>
      </div>
      <!-- 底部按钮 end -->
    </van-popup>
    <!-- 选择人员弹出层 end -->

    <!-- 发起竞价提示弹出框  begin -->
    <van-popup v-model="showJjxx" round closeable :style="{ height: '42%', width: '85%'}">
      <div class="information-tips">
        <div class="tips-icon"><van-image fit="cover" width="100%" height="100%" :src="imageList.tipsimg1" /></div>
        <div class="information-tips-tit">发起竞价成功</div>
        <div class="information-tips-btn">
          <ul>
            <li><van-button type="info" size="large" round @click="goGrzx()">前往个人中心</van-button></li>
          </ul>
        </div>
      </div>
    </van-popup>
    <!-- 发起竞价提示弹出框  end -->
  </div>
</template>

<script land="script" scoped>
import commonList from '@/components/list/commonList1.vue'
import { execJbxx, zgrwgysList } from '@/store/module.js'
import XyUploadPlus from '@/components/upload/uploadPlus'
import { ImagePreview } from 'vant'
export default {
  name: 'BidRule',
  components: {
    commonList,
    XyUploadPlus
  },
  data() {
    return {
      key: 1,
      updatedNum: 0,
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"',
      filegroup: '',
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      xypage: {
        xyCurrentPage: 1, // 当前页
        xyPageSize: 10, // 当前页面展示个数
        xyPageTotal: 0 // 总条数
      },
      AllGysList: [], // 所有可选择供应商列表
      checkIds: [],
      gysmc: '',
      GysList: [], // 搜索后的可选择供应商列表
      fileChangeInfo: {},
      spxxlistcc: [], // 传参时的商品信息
      xzGysList: [], // 选择供应商列表
      spxxlist: [], // 商品信息列表
      num: '1',
      message1: '',
      message2: '',
      message3: '',
      message4: '',
      message5: '',
      message6: '',
      message7: '',
      message8: '',
      message9: '',
      message10: '',
      bidform: { // 页面填写的数据
        czlx: '',
        fbsj: '',
        xmmc: '',
        gysxzfs: '',
        lxrid: '',
        lxrmc: '',
        lxdh: '',
        email: '',
        qpj: '',
        gyszgyq: '',
        bz: '',
        jjsj: '',
        jjkssj: '',
        jjjssj: '',
        sfbjcs: '',
        bjcs: '',
        sfysjj: '',
        ysfptj: '',
        zdycsj: '',
        sfxzyscs: '',
        sfxzbjcs: '',
        ysfpcs: '',
        djfs: '',
        djslorbfb: '',
        gysxxList: [],
        spxxList: [],
        fj: '',
        spImage: '',
        swyq: '',
        jjgz1: '',
        jjgz2: '',
        yscs: '',
        sfrxyscs: '',
        sfrxysjj: '',
        cjbcsz: '',
        jdsl: '',
        lxr: ''
      },
      headerData: {
        title: '设置竞价规则',
        leftUrl: 'back',
        leftText: ' '
      },
      radio: '1',
      radio1: '3',
      radio2: '5',
      radio3: '7',
      radio4: '9',
      value: '1',
      date: '',
      show: false,
      showGys: false,
      showJjxx: false,
      imageList: {
        tipsimg1: require('@/assets/images/icon-success.png'), /* 成功 */
        tipsimg2: require('@/assets/images/icon-info.png'), /* 警告 */
        tipsimg3: require('@/assets/images/icon-error.png') /* 失败 */
      }
    }
  },
  computed: {

  },
  watch: {
    spxxlist() {
      for (let i = 0; i < this.spxxlist.length; i++) {
        const item = this.spxxlist[i]
        if (item.productList) {
          for (let j = 0; j < item.productList.length; j++) {
            const temp = item.productList[j]
            this.spxxlistcc.push({ spImage: temp.imgSrc, id: temp.id, cgml: temp.category_id, wzbm: temp.code, xmmc: temp.name, cgsl: temp.pcount, dj: temp.price_now })
          }
        }
      }
    }
  },
  updated() {
    this.updatedNum++
  },
  created() {
    this.bidform.lxr = this.$store.state.mh.mhUserInfo.name
    this.bidform.lxdh = this.$store.state.mh.mhUserInfo.sj
    this.$bus.$on('change', (res) => {
      this.spxxlist = res
    })
    this.spxxlist = this.$store.state.bid.spxxList
    this.init()
  },

  methods: {
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    // 上传附件回调函数 name:附件名称，fileChanInfo:附件信息
    getFileChangeInfo(name, fileChangeInfo) {
      this.fileChangeInfo[name] = fileChangeInfo
      this.updatedNum++
    },
    init() {
      this.list.postUrl = zgrwgysList
      var category_id = ''
      if (this.gysmc === '') {
        if (this.spxxlist) {
          category_id = this.spxxlist[0].productList[0].category_id
        }
      }
      this.list.reqData = {
        pmbh: category_id,
        yxshtyxydm: '',
        pageno: this.xypage.xyCurrentPage,
        pagerow: this.xypage.xyPageSize,
        gysmc: this.gysmc,
        shtyxydm: '',
        cgml: ''
      }
      this.list.dataName = 'list'
      this.list.noDataText = '暂无更多订单'
    },
    getPrizeLog(data) {
      this.AllGysList = data
      this.GysList = data
    },
    deleteGys(jgbh) {
      for (let i = 0; i < this.xzGysList.length; i++) {
        if (this.xzGysList[i].jgbh === jgbh) {
          this.xzGysList.splice(i, 1)
        }
      }
      for (let i = 0; i < this.checkIds.length; i++) {
        if (this.checkIds[i] === jgbh) {
          this.checkIds.splice(i, 1)
        }
      }
    },
    close() {
      for (let i = 0; i < this.checkIds.length; i++) {
        for (let j = 0; j < this.AllGysList.length; j++) {
          if (this.checkIds[i] === this.AllGysList[j].jgbh) {
            let state = false
            for (let k = 0; k < this.xzGysList.length; k++) {
              if (this.xzGysList[k].jgbh === this.AllGysList[j].jgbh) {
                state = true
              }
            }
            if (state) {
              continue
            } else {
              this.xzGysList.push(this.AllGysList[j])
            }
          }
        }
      }
      this.showGys = false
    },
    onSearch() {
      this.list.reqData.gysmc = this.gysmc
      if (this.list.reqData.gysmc) {
        this.list.reqData.pmbh = ''
      } else {
        if (this.gysmc === '') {
          if (this.spxxlist) {
            this.list.reqData.pmbh = this.spxxlist[0].productList[0].category_id
          }
        }
      }
      this.$bus.$emit('refList1')
    },
    checkGys(index) {
      let state = false
      let index1 = ''
      for (let i = 0; i < this.checkIds.length; i++) {
        if (this.checkIds[i] === this.GysList[index].jgbh) {
          state = true
          index1 = i
        }
      }
      if (state && index1 !== '') {
        this.checkIds.splice(index1, 1)
      } else {
        this.checkIds.push(this.GysList[index].jgbh)
      }
    },
    goBack() {
      if (this.updatedNum > 1) {
        this.$dialog.confirm({
          title: '提示',
          message: '确认离开当前页面吗？'
        }).then(() => {
          this.$router.back()
        }).catch(() => {
        })
      } else {
        this.$router.back()
      }
    },
    // 跳转商品详情
    toProductDetail(product_id, spmc) {
      this.$store.commit('goods/SET_SPMC', spmc)
      this.$store.commit('goods/SET_SPID', product_id)
      this.$router.push({
        name: 'GoodsDetail',
        params: {
          refresh: true
        }
      })
    },
    // 删除商品
    DeleteSp(id) {
      for (var i = 0; i < this.spxxlist.length; i++) {
        var productList = this.spxxlist[i].productList
        for (var j = 0; j < productList.length; j++) {
          if (productList[j].id === id) {
            productList.splice(j, 1)
          }
        }
        if (productList.length === 0) {
          this.spxxlist.splice(i, 1)
        }
      }
    },
    goGrzx() {
      this.$store.commit('mall/SET_ISACTIVE', 3)
      this.$router.push('MallMine')
    },
    // 转换竞价持续时间
    getTime() {
      if (this.bidform.jjsj) {
        const second = (this.date[1] - this.date[0]) / 1000
        const day = Math.floor(second / 60 / 60 / 24)
        const houre = Math.floor(second / 60 / 60 - day * 24)
        const minute = Math.floor(second / 60 - (day * 24 + houre) * 60)
        const seconds = second % 60
        return day + '天' + houre + '时' + minute + '分' + seconds + '秒'
      }
    },
    // 出价方式改变时
    cjfsChange() {
      this.message9 = ''
      this.bidform.jdsl = ''
    },
    check(val) {
      if (val === 0) {
        if (this.bidform.xmmc.length === 0) {
          this.message1 = '请输入项目名称'
          return this.message1
        } else {
          this.message1 = ''
          return ''
        }
      }
      if (val === 1) {
        if (this.bidform.qpj.length === 0) {
          this.message2 = '请输入起拍价格'
          return this.message2
        } else {
          this.message2 = ''
          return ''
        }
      }
      if (val === 2) {
        if (this.bidform.gysxzfs.length === 0) {
          this.message3 = '请选择供应商征集方式'
          return this.message3
        } else {
          this.message3 = ''
          return ''
        }
      }
      if (val === 3) {
        if (this.bidform.email.length === 0) {
          this.message4 = '请填写邮箱'
          return this.message4
        } else if (/^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/.test(this.bidform.email) === false) {
          this.message4 = '邮箱格式不正确'
          return this.message4
        } else {
          this.message4 = ''
          return ''
        }
      }
      if (val === 4) {
        if (this.bidform.jjsj.length === 0) {
          this.message5 = '请选择时间'
          return this.message5
        } else {
          this.message5 = ''
          return ''
        }
      }
      if (val === 5) {
        if (this.bidform.sfxzbjcs === '0') {
          this.bidform.bjcs = ''
        }
        if (this.bidform.sfxzbjcs.length === 0) {
          this.message6 = '请选择是否限制报价次数'
          return this.message6
        } else {
          this.message6 = ''
          return ''
        }
      }
      if (val === 6) {
        if (this.bidform.sfrxyscs === '0') {
          this.bidform.yscs = ''
        }
        if (this.bidform.sfrxyscs.length === 0) {
          this.message7 = '请选择是否允许延时次数'
          return this.message7
        } else {
          this.message7 = ''
          return ''
        }
      }
      if (val === 7) {
        if (this.bidform.sfrxysjj === '0') {
          this.bidform.jjgz1 = ''
          this.bidform.jjgz2 = ''
          this.bidform.yscs = ''
          this.bidform.sfrxyscs = '0'
        }
        if (this.bidform.sfrxysjj.length === 0) {
          this.message8 = '请选择是否允许延时竞价'
          return this.message8
        } else {
          this.message8 = ''
          return ''
        }
      }
      if (val === 8) {
        if (this.bidform.cjbcsz.length === 0) {
          this.message9 = '请设置出价步长方式'
          return this.message9
        } else {
          this.message9 = ''
          return ''
        }
      }
      if (val === 9) {
        if (this.bidform.jdsl.length === 0) {
          this.message10 = '请输入内容'
          return this.message10
        } else {
          this.message10 = ''
          return ''
        }
      }
    },
    showPopup() {
      this.show = true
    },
    showXmztPopup() {
      this.show1 = true
    },
    showcalendar() {
      this.show2 = true
    },
    showGdczPopup() {
      this.show3 = true
    },
    showGysPopup() {
      this.checkIds = []
      this.gysmc = ''
      this.onSearch()
      this.showGys = true
    },
    zhTime(time) {
      const year = time.getFullYear()
      const month = (time.getMonth() + 1) < 10 ? ('0' + (time.getMonth() + 1)) : (time.getMonth() + 1)
      const day = time.getDate() < 10 ? ('0' + time.getDate()) : time.getDate()
      const hour = time.getHours() < 10 ? ('0' + time.getHours()) : time.getHours()
      const minute = time.getMinutes() < 10 ? ('0' + time.getMinutes()) : time.getMinutes()
      const second = time.getSeconds() < 10 ? ('0' + time.getSeconds()) : time.getSeconds()
      const sj = year + '' + month + '' + day + '' + hour + '' + minute + '' + second
      return sj
    },
    formatDate(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    },
    calPrice(pconut, cart_id, indexs, index) {
      if (pconut === '-') {
        this.spxxlist[indexs].productList[index].pcount = 1
      }
      if (pconut > 99999) {
        this.spxxlist[indexs].productList[index].pcount = 99999
        pconut = 99999
        this.key++
      }
      // saveCart.data = { id: cart_id, pcount: pconut }
      // this.asyncApiModule(saveCart).then(res => {
      // })
      //   .catch(() => {
      //   })
      this.spxxlistcc = []
      for (let i = 0; i < this.spxxlist.length; i++) {
        const item = this.spxxlist[i]
        if (item.productList) {
          for (let j = 0; j < item.productList.length; j++) {
            const temp = item.productList[j]
            this.spxxlistcc.push({ spImage: temp.imgSrc, id: temp.id, cgml: temp.category_id, wzbm: temp.code, xmmc: temp.name, cgsl: temp.pcount, dj: temp.price_now })
          }
        }
      }
    },
    onConfirm(date) {
      const [start, end] = date
      this.date = date
      this.show = false
      this.bidform.jjkssj = this.zhTime(start)
      this.bidform.jjjssj = this.zhTime(end)
      this.bidform.jjsj = `${this.formatDate(start)} - ${this.formatDate(end)}`
      console.log('bidform-------------------------', this.bidform)
    },
    // 提交当前页面输入数据
    gobidsuccess(czlx) {
      var temp = ''
      for (let i = 0; i < 10; i++) {
        temp += this.check(i)
      }
      if (temp !== '') {
        this.$toast.fail('请完善页面信息')
        return
      }
      if (this.bidform.sfxzbjcs === '1' && this.bidform.bjcs <= 0) {
        this.$toast('请输入报价次数！')
        return
      }
      if (this.bidform.sfrxyscs === '1') {
        if (this.bidform.jjgz1 <= 0 || this.bidform.jjgz2 <= 0) {
          this.$toast('请输入延时竞价规则！')
          return
        }
      }
      if (this.bidform.sfrxyscs === '1' && this.bidform.yscs <= 0) {
        this.$toast('请输入延时次数！')
        return
      }
      if (this.bidform.gysxzfs === '2' && this.xzGysList.length < 3) {
        this.$toast.fail('至少选择三家供应商！')
        return
      }
      const arr = []
      for (let k = 0; k < this.xzGysList.length; k++) {
        const item = {}
        item.gysid = this.xzGysList[k].jgbh
        item.ztdm = this.xzGysList[k].shtyxydm
        item.gysmc = this.xzGysList[k].gsmc
        item.lxr = this.xzGysList[k].lxrmc
        item.lxfs = this.xzGysList[k].lxrdh
        item.email = this.xzGysList[k].email
        arr.push(item)
      }
      execJbxx.data = {
        czlx: czlx,
        fbsj: this.zhTime(new Date()),
        xmmc: this.bidform.xmmc,
        gysxzfs: this.bidform.gysxzfs,
        lxrid: this.$store.state.mh.mhUserInfo.id,
        lxrmc: this.bidform.lxr,
        lxdh: this.bidform.lxdh,
        email: this.bidform.email,
        qpj: this.bidform.qpj,
        bz: this.bidform.bz,
        jjkssj: this.bidform.jjkssj,
        jjjssj: this.bidform.jjjssj,
        sfbjcs: this.bidform.sfxzbjcs,
        bjcs: this.bidform.bjcs,
        sfysjj: this.bidform.sfrxysjj,
        ysfptj: this.bidform.jjgz1,
        zdycsj: this.bidform.jjgz2,
        sfxzyscs: this.bidform.sfrxysjj === '1' ? this.bidform.sfrxyscs : '',
        ysfpcs: this.bidform.sfrxysjj === '1' ? this.bidform.yscs : '',
        djfs: this.bidform.cjbcsz,
        djslorbfb: this.bidform.jdsl,
        gysxxList: arr,
        spxxList: this.spxxlistcc,
        fileChangeInfo: this.fileChangeInfo
      }
      this.asyncApiModule(execJbxx).then(res => {
        this.showJjxx = true
        // this.$toast.success('保存成功')
      }).catch(error => {
        console.log('error===', error)
      })
    }
  }

}
</script>

